.bc {
  li {
    display: inline;
    font-size: 0.75rem;
    a {
      color: #0275d8;
      text-decoration: none;
    }
    a:hover {
      color: #01447e;
      text-decoration: underline;
    }
  }
  li + li:before {
    display: inline-block;
    padding: 4px;
    color: black;
    content: "";
    background: url("https://vuemodel-cdn.madstreetden.com/model_library_assets/divider.svg")
      no-repeat center center/cover;
    width: 1rem;
  }
}
.searchbox {
  width: 300px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #e2e2e2;
  border-radius: 5px;
}
.search-box-wrapper input {
  border: 0;
  width: 94%;
  outline: none;
  padding: 0 8px;
  height: 40px;
  background-color: transparent;
}
#btn {
  display: flex;
  align-items: center;
  justify-content: center;
}
.light {
  color: #8a8eb1 !important;
  text-decoration: none !important;
}
.light:hover {
  color: #8a8eb1 !important;
  text-decoration: none !important;
}
.disabled-link {
  pointer-events: none;
}
